* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.loader {
  height: 32px;
  width: 200px;
  color: red;
  background: #fff;
  border: 2px solid #fff;
  display: block;
  position: relative;
  overflow: hidden;
}

.loader:before {
  content: "";
  width: 0;
  height: 100%;
  background: red;
  animation: loading 10s linear infinite;
  position: absolute;
  top: 0;
  left: 0;
}

.loader:after {
  content: "";
  width: 100%;
  height: 100%;
  text-align: center;
  color: #0ff;
  mix-blend-mode: difference;
  font-size: 24px;
  line-height: 32px;
  animation: percentage 10s linear infinite;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes loading {
  0% {
    width: 0;
  }

  100% {
    width: 100%;
  }
}

@keyframes percentage {
  0% {
    content: "0%";
  }

  5% {
    content: "5%";
  }

  10% {
    content: "10%";
  }

  20% {
    content: "20%";
  }

  30% {
    content: "30%";
  }

  40% {
